.teachers {
  padding-top: 200px;
}
@media (max-width: 960px) {
  .teachers {
    padding-top: 120px;
  }
}
@media (max-width: 760px) {
  .teachers {
    padding-top: 100px;
  }
}
@media (max-width: 425px) {
  .teachers {
    padding-top: 60px;
  }
}
.teachers__title {
  font-family: var(--font-family);
  color: var(--light-color);
  font-weight: 700;
  font-size: 48px;
  line-height: 1.3;
  margin-bottom: 110px;
  padding-left: 125px;
  position: relative;
}
@media (max-width: 960px) {
  .teachers__title {
    font-size: 32px;
    padding-left: 110px;
    margin-bottom: 100px;
  }
}
@media (max-width: 760px) {
  .teachers__title {
    padding-left: 80px;
    margin-bottom: 80px;
  }
}
@media (max-width: 425px) {
  .teachers__title {
    font-size: 28px;
    margin-bottom: 50px;
    padding-left: 0;
    padding-top: 50px;
  }
}
.teachers__title::before {
  content: "";
  display: block;
  position: absolute;
  background: url(../img/icons/icon-heading.svg) no-repeat;
  background-size: cover;
  width: 95px;
  height: 95px;
  left: 0;
  top: -50%;
  transform: rotate(320deg);
}
@media (max-width: 960px) {
  .teachers__title::before {
    width: 80px;
    height: 80px;
  }
}
@media (max-width: 760px) {
  .teachers__title::before {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 425px) {
  .teachers__title::before {
    width: 50px;
    height: 50px;
    top: 0;
  }
}
.teachers__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
}
@media (max-width: 760px) {
  .teachers__wrap {
    grid-template-columns: 1fr 1fr;
    gap: 50px 20px;
  }
}
@media (max-width: 425px) {
  .teachers__wrap {
    grid-template-columns: 1fr;
  }
}
.teachers__item {
  text-align: center;
}
.teachers__item-avatar {
  width: 190px;
  height: 190px;
  border-radius: 100%;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 40px;
}
@media (max-width: 760px) {
  .teachers__item-avatar {
    width: 150px;
    height: 150px;
    margin-bottom: 30px;
  }
}
.teachers__item-img {
  width: 100%;
  height: 100%;
  object-position: top center;
  object-fit: cover;
}
.teachers__item-name {
  font-family: var(--font-family);
  color: var(--light-color);
  font-weight: 500;
  font-size: 24px;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 30px;
}
@media (max-width: 960px) {
  .teachers__item-name {
    font-size: 21px;
  }
}
@media (max-width: 760px) {
  .teachers__item-name {
    font-size: 18px;
    margin-bottom: 20px;
  }
}
.teachers__item-prof {
  font-family: var(--font-family);
  color: var(--light-color-opacity);
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  margin-bottom: 30px;
}
@media (max-width: 760px) {
  .teachers__item-prof {
    margin-bottom: 20px;
  }
}
.teachers__item-link {
  display: inline-block;
  font-family: var(--font-family);
  padding: 10px 30px;
  text-decoration: none;
  color: var(--light-color);
  font-size: 16px;
  line-height: 1.2;
  border: 1px solid var(--light-color);
  border-radius: 50px;
  text-align: center;
  transition: color 0.2s ease-in, background-color 0.2s ease-in;
}
.teachers__item-link:hover {
  background-color: #fff;
  color: var(--second-bg);
}